"use client"
import CircularGallery from './CircularGallery'
import { useState } from "react"
import { X } from "lucide-react"
import { galleryData } from "../data/gallery"


export default function Gallery() {
  const [selectedImage, setSelectedImage] = useState(null)

  return (
    <section className="pt-10 sm:pt-12 pb-16 bg-transparent">
      <div className="max-w-7xl mx-auto px-4">
        {/* Title with subtle glow background */}
        <div className="relative mb-8">
          {/* soft glow backdrop */}
          <div className="absolute inset-x-0 top-1/2 -translate-y-1/2 h-32 bg-gradient-to-r from-transparent via-accent/10 to-transparent blur-3xl pointer-events-none" />
          
          <h2 className="relative text-4xl sm:text-5xl font-cinzel text-white text-center carnival-font drop-shadow-lg">
            Gallery
          </h2>
        </div>

        {/* Circular Gallery */}
        <div style={{ height: '700px', position: 'relative' }}>
          <CircularGallery bend={2} textColor="#ffffff" borderRadius={0.05} scrollEase={0.02} />
        </div>
      </div>

      {/* Lightbox Modal */}
      {selectedImage && (
        <div className="fixed inset-0 bg-black/80 flex items-center justify-center p-4 z-50">
          <div className="relative max-w-4xl w-full">
            <button
              onClick={() => setSelectedImage(null)}
              className="absolute -top-12 right-0 text-white hover:text-accent transition-colors"
            >
              <X size={32} />
            </button>
            <img
              src={selectedImage.url || "/placeholder.svg"}
              alt={selectedImage.alt}
              className="w-full h-auto rounded-lg shadow-2xl"
              onError={(e) => {
                e.target.src = `/placeholder.svg?height=700&width=800&query=${selectedImage.alt}`
              }}
            />
          </div>
        </div>
      )}
    </section>
  )
}